.spinbox {
  width: 100px;
  user-select: none;
}

.spinbox div:first-child,
.spinbox div:last-child {
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.spinbox div:first-child:hover,
.spinbox div:last-child:hover {
  background: #bed49f;
}

.card-text {
  color: #01551b !important;
}

textarea{
min-height:135px;
